import { useEffect } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import AOS from "aos";
import "aos/dist/aos.css";
import Header from "./Components/Header/Header";
import Footer from "./Components/Footer/Footer";
import Home from "./View/Home/Home";
import PrivateInvestment from "./View/PrivateInvestment/PrivateInvestment";
import ForeignExchange from "./View/ForeignExchange/ForeignExchange";
import Stock from "./View/Stock/Stock";
import BlockChain from "./View/BlockChain/BlockChain";
import CryptocurrencyMarketMakers from "./View/CryptocurrencyMarketMakers/CryptocurrencyMarketMakers";

import Investment from "./View/Investment/Investment";
import Profile from "./View/Profile/Profile";

import "./App.scss";
import "antd/dist/antd.css";

const App = () => {
  useEffect(() => {
    AOS.init();
  }, []);
  return (
    <div className={"App"}>
      <Header />
      <Switch>
        <Route path="/" exact>
          <Home />
        </Route>
        <Route path="/PrivateInvestment">
          <PrivateInvestment />
        </Route>
        <Route path="/ForeignExchange">
          <ForeignExchange />
        </Route>
        <Route path="/Stock">
          <Stock />
        </Route>
        <Route path="/BlockChain">
          <BlockChain />
        </Route>
        <Route path="/CryptoMarketMaking">
          <CryptocurrencyMarketMakers />
        </Route>
        <Route path="/investment">
          <Investment />
        </Route>
        <Route path="/profile">
          <Profile />
        </Route>
        <Redirect to="/" />
      </Switch>
      <Footer />
    </div>
  );
};

export default App;
