import React, { useCallback, useEffect, useState } from "react";
import { Checkbox, Input, message } from "antd";
import MyDocument from "../../Components/PDF/PdfGenerate";
import { BlobProvider } from "@react-pdf/renderer";
import Button from "../../Components/UI/Button/Button";
import MainContainer from "../../Components/UI/MainContainer/MainContainer";
import { useInviterRecords, useIsRegistered } from "../../hooks/useInvitation";
import { useGlobalConfig, useInvest } from "../../hooks/useInvestPool";

import {
  useTokenAllowance,
  useTokenApprove,
  useTokenBalance,
} from "../../hooks/useToken";

import { useWallet } from "../../hooks/useWallet";
// import CustomModal from "../../Components/UI/CustomModal/CustomModal";
import styles from "./Investment.module.scss";
import { INVESTPOOL_ADDRESS, TOKENS } from "../../constants/addresses";

const SAFT_LINK_TEXT = "I Agree BFG Simple Agreement for Future Tokens";

const Investment = () => {
  const [checked, setChecked] = useState(false);
  const [pdfProps, setPdfProps] = useState({});
  const [investAmount, setInvestAmount] = useState("0");
  const myDoc = <MyDocument props={pdfProps} />;
  const { data: isRegistered, isLoading: isRegisteredLoading } =
    useIsRegistered();
  const { data: globalConfig, isLoading: globalConfigLoading } =
    useGlobalConfig();
  const { data: inviterRecords, isLoading: inviterRecordsLoading } =
    useInviterRecords();
  const { data: nativeBalance } = useTokenBalance(TOKENS.NATIVE);
  const { data: acceptBalance } = useTokenBalance(TOKENS.ACCEPT);
  const { data: acceptTokenAllowance } = useTokenAllowance(
    TOKENS.ACCEPT,
    INVESTPOOL_ADDRESS
  );
  const { state: approveState, approve } = useTokenApprove(TOKENS.ACCEPT);
  const { state: investState, invest } = useInvest();
  const { minimalSourceAmount, price } = globalConfig;
  const { nickname, email } = inviterRecords;
  const { account } = useWallet();

  const updatePdfProps = useCallback(() => {
    if (!(account && isRegistered)) {
      return;
    }
    const date = new Date();
    setPdfProps({
      purchaseAmount: Number(investAmount),
      year: date.getFullYear(),
      month: date.getUTCMonth() + 1,
      day: date.getUTCDate(),
      amountOfBGF: Number(investAmount) * price,
      purchaserName: checked ? nickname : "",
      purchaserEmail: checked ? email : "",
    });
  }, [investAmount, checked, account, email, isRegistered, nickname, price]);

  useEffect(() => {
    updatePdfProps();
  }, [updatePdfProps]);

  useEffect(() => {
    if (minimalSourceAmount) {
      setInvestAmount(minimalSourceAmount);
    }
  }, [minimalSourceAmount]);

  const handleCheck = (e) => {
    setChecked(e.target.checked);
  };

  const handleInvestAmountChange = (e) => {
    const value = e.target.value;
    if (Number.isNaN(Number(value))) {
      return;
    }
    setInvestAmount(value);
  };

  const handleInvest = () => {
    if (
      isRegisteredLoading ||
      globalConfigLoading ||
      inviterRecordsLoading ||
      investState?.isLoading
    ) {
      return;
    }
    if (!(account && isRegistered)) {
      message.info("Please connect to wallet and sign up");
      return;
    }
    if (Number(investAmount < minimalSourceAmount)) {
      message.info(`The minimum investment is ${minimalSourceAmount} USDT`);
      return;
    }
    if (!checked) {
      message.info(`You have to agree to the BGF SAFT`);
      return;
    }
    if (nativeBalance < 0.01) {
      message.info("Need gas");
      return;
    }
    if (acceptBalance < investAmount) {
      message.info(`Your USDT balance is less than ${investAmount}`);
      return;
    }
    invest(investAmount);
  };

  const handleApprove = async () => {
    if (approveState?.isLoading) {
      return;
    }
    if (!account) {
      message.info("Please connect to wallet");
      return;
    }

    // check for gas
    if (nativeBalance < 0.01) {
      message.info("Need gas");
      return;
    }
    approve(INVESTPOOL_ADDRESS);
  };

  return (
    <MainContainer className={styles.Investment}>
      <div className={styles.imgWrap}>
        <img src="/static/images/Investment/invest.png" alt="invest" />
      </div>
      <div className={styles.contentWrap}>
        <div className={styles.investWrap}>
          <h2 className={styles.title}>Investment</h2>
          <div className={styles.inputItem}>
            <h2>Investment Amount</h2>
            <Input
              suffix="USDT"
              value={investAmount}
              onChange={handleInvestAmountChange}
            />
            <p>• The minimum investment is {minimalSourceAmount} USDT</p>
          </div>
          <div className={styles.inputItem}>
            <div className={styles.checkboxWrap}>
              <Checkbox onChange={handleCheck} />
              &nbsp;&nbsp;
              <BlobProvider document={myDoc}>
                {({ url, loading }) => (
                  <a
                    href={url}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={
                      loading ||
                      isRegisteredLoading ||
                      globalConfigLoading ||
                      inviterRecordsLoading
                        ? { pointerEvents: "none" }
                        : null
                    }
                  >
                    {SAFT_LINK_TEXT}
                  </a>
                )}
              </BlobProvider>
            </div>

            <p>• By clicking submit, you agree to the BGF SAFT</p>
          </div>
          {acceptTokenAllowance ? (
            <Button
              className={styles.submitButton}
              onClick={handleInvest}
              loading={investState?.isLoading}
            >
              Invest
            </Button>
          ) : (
            <Button
              className={styles.submitButton}
              onClick={handleApprove}
              loading={approveState?.isLoading}
            >
              Allow USDT
            </Button>
          )}
        </div>
      </div>
      {/* <CustomModal
        className={styles.popup}
        visible={!inviterRecordsLoading && !(account && isRegistered)}
      >
        <p>Please connect to wallet and sign up</p>
        <img src="/static/images/Investment/404.png" alt="not found" />
      </CustomModal> */}
    </MainContainer>
  );
};

export default Investment;
