import React, { useEffect, useState } from "react";
import { UpCircleOutlined } from "@ant-design/icons";
import styles from "./ScrollToTop.module.scss";

const ScrollToTop = () => {
  const [isVisible, setIsVisible] = useState(false);

  // Show button when page is scrolled upto given distance
  const toggleVisibility = () => {
    if (window.pageYOffset > 300) {
      setIsVisible(true);
    } else {
      setIsVisible(false);
    }
  };

  // Set the top coordinate to 0
  // make scrolling smooth
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  useEffect(() => {
    window.addEventListener("scroll", toggleVisibility);
    return () => window.removeEventListener("scroll", toggleVisibility);
  }, []);

  return (
    <div className={styles.scrollToTop}>
      {isVisible && (
        <div onClick={scrollToTop}>
          {/* <img
            style={{
              width: '40px',
              filter:
                'brightness(0) saturate(100%) invert(49%) sepia(19%) saturate(1220%) hue-rotate(82deg) brightness(100%) contrast(87%)',
            }}
            className={styles.scrollToTopIcon}
            src="/static/images/icons/top.svg"
            alt="top"
          /> */}
          <UpCircleOutlined style={{ fontSize: "36px" }} />
        </div>
      )}
    </div>
  );
};

export default ScrollToTop;
