import React, { useState, useEffect } from "react";
import { message, Progress } from "antd";

import Button from "../../../UI/Button/Button";
import { useClaim } from "../../../../hooks/useInvestPool";
import { formatNumber, formatOutput } from "../../../../utils/utils";
import styles from "./InvestmentItem.module.scss";
import { TOKENS } from "../../../../constants/addresses";
import { useTokenBalance } from "../../../../hooks/useToken";

const InvestmentItem = ({ record, globalConfig, inviterRecords }) => {
  const { data: nativeBalance } = useTokenBalance(TOKENS.NATIVE);
  const { state: claimState, claim } = useClaim();
  const [totalUnlockedRatio, setTotalUnlockedRatio] = useState(0);
  const [unlockedClaimableRatio, setUnlockedClaimableRatio] = useState(0);
  const nextProcessIndex = record?.nextProcessIndex;
  const releaseConfig = globalConfig?.releaseConfig;
  // console.log(record, globalConfig, inviterRecords);

  const handleClaim = () => {
    if (claimState.isLoading) {
      return;
    }
    if (nativeBalance < 0.01) {
      message.info("Need gas");
      return;
    }
    if (!unlockedClaimableRatio) {
      message.info("Nothing to claim");
      return;
    }
    claim([record.recordId]);
  };

  const investTimestampString = formatOutput(
    new Date(record.investTimestamp * 1000).toISOString().substring(0, 10)
  );

  const nextUnlockTimestamp =
    globalConfig?.releaseStartTime +
    releaseConfig?.[nextProcessIndex]?.timestampOffset;

  const nextUnlockTimestampString = formatOutput(
    nextUnlockTimestamp &&
      new Date(nextUnlockTimestamp * 1000).toISOString().substring(0, 10)
  );

  const nextUnlockRatio =
    releaseConfig?.[nextProcessIndex]?.ratioPerThousand / 1000;

  const nextUnlockTargetAmount = record?.targetAmount * nextUnlockRatio;

  //  total unlocked ratio
  useEffect(() => {
    if (releaseConfig?.length && nextProcessIndex) {
      const _totalUnlockedRatio =
        releaseConfig
          .filter((_, index) => index < nextProcessIndex)
          .reduce((accum, item) => accum + item.ratioPerThousand, 0) / 1000;
      setTotalUnlockedRatio(_totalUnlockedRatio);
    }
  }, [nextProcessIndex, releaseConfig]);

  useEffect(() => {
    if (
      releaseConfig?.length &&
      nextProcessIndex &&
      globalConfig?.releaseStartTime
    ) {
      const _unlockedClaimableRatio =
        releaseConfig
          .filter(
            (item, index) =>
              index < nextProcessIndex &&
              globalConfig?.releaseStartTime + item.timestampOffset >
                record.lastClaimTimestamp
          )
          .reduce((accum, item) => accum + item.ratioPerThousand, 0) / 1000;
      setUnlockedClaimableRatio(_unlockedClaimableRatio);
    }
  }, [
    nextProcessIndex,
    globalConfig?.releaseStartTime,
    record.lastClaimTimestamp,
    releaseConfig,
  ]);

  return (
    <div className={styles.InvestmentItem}>
      <div className={styles.statsWrap}>
        <div className={styles.infoItem}>
          <h2>Investment Time:</h2>
          <p>{investTimestampString}</p>
        </div>
        <div className={styles.infoItem}>
          <h2>Investment Amount:</h2>
          <p>{formatNumber(record?.sourceAmount)} USDT</p>
        </div>
      </div>
      <div className={styles.unlockWrap}>
        <div className={styles.progressWrap}>
          <div className={styles.infoItem}>
            <h2>BGF Token:</h2>
            <p>
              {formatNumber(totalUnlockedRatio * record?.targetAmount)}/
              {formatNumber(record?.targetAmount)}
            </p>
          </div>
          <Progress
            percent={totalUnlockedRatio}
            showInfo={false}
            strokeColor={{
              "0%": "#FDC231",
              "100%": "#F47735",
            }}
            strokeWidth={15}
            trailColor={"white"}
          />
        </div>
        <div className={styles.infoItem}>
          <h2 className={styles.state}>Unlocked: </h2>
          <p>{formatNumber(unlockedClaimableRatio)} BGF</p>
          <Button
            onClick={handleClaim}
            loading={claimState.isLoading}
            disabled={!unlockedClaimableRatio}
          >
            Claim
          </Button>
        </div>
        <div className={styles.infoItem}>
          <h2>Next Unlock:</h2>
          <p>{formatNumber(nextUnlockTargetAmount)} BGF</p>
          <p>{nextUnlockTimestampString}</p>
          {/* <p>{new Date(record?.start * 1000).toISOString()}</p> */}
          {/* <p>1000 BGF</p> */}
        </div>
      </div>
    </div>
  );
};

InvestmentItem.propTypes = {};

export default InvestmentItem;
