import { useEffect, useReducer } from "react";
import { ethers } from "ethers";
import { message } from "antd";

import { readContractReducer, FETCH_STATE } from "../utils/utils";
import { useInvestPoolContract, useMulticallContract } from "./useContract";
import { useIsMounted } from "./useIsMounted";
import { useWallet } from "./useWallet";
import { useUpdate } from "./useUpdate";
import { multicallHelper } from "../utils/multicall";

import INVESTPOOL_ABI from "../abis/investPool.json";

const decodeReleaseConfig = (config) => ({
  timestampOffset: config.timestampOffset.toNumber(),
  ratioPerThousand: config.ratioPerThousand.toNumber(),
});

const decodeGlobalConfig = (config) => {
  return {
    inviterRecursiveLoop: config.inviterRecursiveLoop.toNumber(),
    startTimestamp: config.startTimestamp.toNumber(),
    endTimestamp: config.endTimestamp.toNumber(),
    price: config.price.toNumber(),
    releaseStartTime: config.releaseStartTime.toNumber(),
    releaseConfig: config.releaseConfig.map((item) =>
      decodeReleaseConfig(item)
    ),
    minimalSourceAmount: Number(
      ethers.utils.formatEther(config.minimalSourceAmount)
    ),
    //0,1,2, in 10000
    bonusRate: config.bonusRate.map((item) => item.toNumber() / 10000),
  };
};

const decodeInvestRecord = (record) => {
  const { utils } = ethers;
  return {
    recordId: record.recordId.toNumber(),
    sourceAmount: Number(utils.formatEther(record.sourceAmount)),
    targetAmount: Number(utils.formatEther(record.targetAmount)),
    investTimestamp: record.investTimestamp.toNumber(),
    nextProcessIndex: record.nextProcessIndex.toNumber(),
    lastClaimTimestamp: record.lastClaimTimestamp.toNumber(),
    done: record.done,
  };
};

const decodePersonRecord = (record) => {
  const { utils } = ethers;
  return {
    bonus: Number(utils.formatEther(record.bonus)),

    //while invitee **invest** source, add this amount
    inviteeSourceInvestAmounts: record.inviteeSourceInvestAmounts.map((item) =>
      Number(utils.formatEther(item))
    ),
    //while invitee **claim** target, add this amount
    inviteeTargetClaimAmounts: record.inviteeTargetClaimAmounts.map((item) =>
      Number(utils.formatEther(item))
    ),

    //while invitee **claim** target, add this the target bonus amount and update bonus
    inviteeTargetBonusAmounts: record.inviteeTargetBonusAmounts.map((item) =>
      Number(utils.formatEther(item))
    ),

    //total source invested by self, while invest
    sourceInvested: Number(utils.formatEther(record.sourceInvested)),
    //total target claimed by self, while claim
    targetClaimed: Number(utils.formatEther(record.targetClaimed)),

    //total bonus target claimed by self, while takeBonus
    bonusTaken: Number(utils.formatEther(record.bonusTaken)),
  };
};

export const useInvestRecords = (address) => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: {},
  });
  const investPoolContract = useInvestPoolContract();
  const multicallContract = useMulticallContract();
  const isMounted = useIsMounted();
  const { account } = useWallet();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (
          !(
            investPoolContract &&
            multicallContract &&
            ((address && ethers.utils.isAddress(address)) || account)
          )
        ) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        // fetch person record
        const personRecord = decodePersonRecord(
          await investPoolContract.getPerson(address || account)
        );
        // fetch live records
        const liveRecordsValues = (
          await investPoolContract.liveRecordsValues(address || account)
        ).map((item) => item.toNumber());
        const liveRecordsCall = liveRecordsValues.map((item) => [
          investPoolContract.address,
          "recordDetails",
          [item],
        ]);
        const liveRecordsRes = (
          await multicallHelper(
            multicallContract,
            INVESTPOOL_ABI.abi,
            liveRecordsCall
          )
        )
          .flat()
          .map((item) => decodeInvestRecord(item));
        // console.log(personRecord, liveRecordsRes);
        if (isMounted.current) {
          dispatch({
            type: FETCH_STATE.SUCCESS,
            payload: {
              personRecord,
              liveRecords: liveRecordsRes,
            },
          });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [
    investPoolContract,
    isMounted,
    account,
    address,
    updateByTimer,
    multicallContract,
  ]);

  return state;
};

export const useGlobalConfig = () => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: {},
  });
  const investPoolContract = useInvestPoolContract();
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!investPoolContract) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = decodeGlobalConfig(
          await investPoolContract.getGlobalConfig()
        );
        if (isMounted.current) {
          dispatch({
            type: FETCH_STATE.SUCCESS,
            payload: data,
          });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [investPoolContract, isMounted, updateByTimer]);

  return state;
};

export const useInvest = () => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const investPoolContract = useInvestPoolContract();
  const isMounted = useIsMounted();

  const invest = async (amount) => {
    console.log("invest", amount);
    try {
      const { utils } = ethers;
      if (!(investPoolContract && amount)) {
        return;
      }
      dispatch({ type: FETCH_STATE.INIT });
      const tx = await investPoolContract.invest(
        utils.parseEther(amount.toString()),
        utils.formatBytes32String(""),
        "",
        ""
      );
      message.info("Request sent");
      console.log("tx", tx);
      const receipt = await tx.wait();
      message.success("Transaction completed");
      console.log("receipt", receipt);
      if (isMounted.current) {
        dispatch({ type: FETCH_STATE.SUCCESS, payload: null });
      }
    } catch (error) {
      console.log(error);
      message.error("Transaction failed");
      if (isMounted.current) {
        dispatch({ type: FETCH_STATE.FAILURE });
      }
    }
  };

  return { state, invest };
};

