import React, { useState, useEffect } from "react";
import { Dropdown, Modal, Menu } from "antd";
import { MenuOutlined } from "@ant-design/icons";
import { useIsMobileView } from "../../hooks/useIsMobileView";
import "antd/dist/antd.css";
import classNames from "classnames";
import { NavHashLink } from "react-router-hash-link";
import { useTranslation } from "react-i18next";
import ProductMenu from "./ProductMenu/ProductMenu";
// import MainContainer from "../UI/MainContainer/MainContainer";
// import HeaderMenu from "./Menu/HeaderMenu";

import styles from "./Header.module.scss";
// import Button from "../UI/Button/Button";
import { scrollWithOffset } from "../../utils/utils";
import ProfileButton from "../Profile/ProfileButton/ProfileButton";
import Aos from "aos";
import WalletButton from "./WalletButton/WalletButton";
import { useWallet } from "../../hooks/useWallet";

const langs = [
  { key: "en", label: "English" },
  { key: "zh", label: "简体中文" },
  { key: "ar", label: "عربي" }, // de: { label: "Deutsch", icon: "de" }, // el: { label: "Eλληνικά", icon: "el" },
  { key: "es", label: "Español" }, // fi: { label: "Suomi", icon: "fi" },
  { key: "fr", label: "Français" }, // zh_HANT: { label: '繁體中文' }, // ja: { label: "日本語", icon: "ja" }, // pt: { label: "Português", icon: "pt" }, // it: { label: "Italiano", icon: "it" }, // ro: { label: "Română", icon: "ro" }, // ko: { label: "한국의", icon: "ko" },
  { key: "ru", label: "Русский" }, // bn: { label: "বাংলা", icon: "bn" }, // hr: { label: 'hrvatski' },
  { key: "hi", label: "हिन्दी" }, // hu: { label: "Magyar", icon: "hu" }, // vi: { label: "Tiếng Việt", icon: "vi" },
];

const NAV_MENU = [
  {
    id: 1,
    text: null,
    textKey: "header-home",
    to: "/#top",
    overlay: null,
  },
  {
    id: 2,
    text: null,
    textKey: "header-about",
    to: "/#about",
    overlay: null,
  },
  {
    id: 3,
    text: null,
    textKey: "header-product",
    to: null,
    overlay: ProductMenu,
  },
  {
    id: 4,
    text: null,
    textKey: "header-team",
    to: "/#team",
    overlay: null,
  },
  {
    id: 5,
    text: null,
    textKey: "header-contact",
    to: "/#contact",
    overlay: null,
  },
  {
    id: 6,
    text: null,
    textKey: "",
    to: "",
    overlay: null,
  },
  {
    id: 7,
    text: null,
    textKey: "header-investment",
    to: "/investment",
    overlay: null,
  },
];

const Header = () => {
  const isMobileView = useIsMobileView();
  const [menuCollapsed, setMenuCollapsed] = useState(true);
  const [currentLang, setCurrentLang] = useState("en");
  const { i18n, t } = useTranslation();
  const { account } = useWallet();

  const langMenu = (
    <Menu
      items={langs}
      onClick={(item) => {
        i18n.changeLanguage(item.key);
      }}
      className={styles.menu}
    />
  );

  useEffect(() => {
    setCurrentLang(i18n.resolvedLanguage);
  }, [i18n.resolvedLanguage]);

  useEffect(() => {
    setMenuCollapsed(true);
  }, [isMobileView]);

  const toggleMenu = () => {
    if (isMobileView) {
      setMenuCollapsed((prev) => !prev);
    }
    setTimeout(() => {
      Aos.refresh();
    }, 500);
  };

  const menuWrap = (
    <div className={styles.menuWrap}>
      <div className={styles.navWrap}>
        {NAV_MENU.map((item) => {
          if (!(item.overlay || item.to)) {
            return <div key={item.id} className={styles.verticalBar} />;
          }
          if (item.overlay) {
            const Overlay = item.overlay;
            return (
              <Dropdown
                key={item.id}
                overlay={<Overlay onClick={toggleMenu} />}
              >
                <div className={styles.link}>
                  {item.text || t(item.textKey)}
                </div>
              </Dropdown>
            );
          }
          return (
            <NavHashLink
              className={(isActive) =>
                classNames(styles.link, {
                  [styles.active]: isActive,
                })
              }
              key={item.id}
              smooth
              to={item.to}
              onClick={toggleMenu}
              scroll={(el) => scrollWithOffset(el)}
            >
              {item.text || t(item.textKey)}
            </NavHashLink>
          );
        })}
      </div>
      <div className={styles.walletWrap}>
        {account && <ProfileButton onClick={toggleMenu} />}
        <WalletButton onClick={toggleMenu} />
      </div>
    </div>
  );

  return (
    <>
      <div className={styles.placeholder}></div>
      <div className={styles.Header}>
        {isMobileView && (
          <MenuOutlined className={styles.hamburger} onClick={toggleMenu} />
        )}
        <img
          className={styles.logo}
          src="./static/images/Header/logo.png"
          alt="BGF"
        />
        {!isMobileView && menuWrap}
        <Dropdown overlay={langMenu}>
          <img
            className={styles.langButton}
            src={`/static/images/Header/flags/${currentLang}.png`}
            alt={currentLang}
          />
        </Dropdown>
      </div>
      <Modal
        title={null}
        visible={!menuCollapsed}
        onOk={toggleMenu}
        onCancel={toggleMenu}
        wrapClassName={styles.HeaderMenuWrapper}
        width={"100%"}
        centered
        closable={true}
        footer={null}
      >
        {menuWrap}
        {/* <HeaderMenu toggleMenu={toggleMenu} /> */}
      </Modal>
    </>
  );
};

export default Header;
