export const useIsRegistered = (address) => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const invitationContract = useInvitationContract();
  const isMounted = useIsMounted();
  const { account } = useWallet();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (
          !(
            invitationContract &&
            ((address && ethers.utils.isAddress(address)) || account)
          )
        ) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await invitationContract.isRegistered(address || account);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.SUCCESS, payload: data });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [invitationContract, isMounted, account, address, updateByTimer]);

  return state;
};

export const useInviterRecords = (address) => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: {},
  });
  const invitationContract = useInvitationContract();
  const isMounted = useIsMounted();
  const { account } = useWallet();
  const { updateByTimer } = useUpdate();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (
          !(
            invitationContract &&
            ((address && ethers.utils.isAddress(address)) || account)
          )
        ) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });
        const data = await invitationContract.inviterRecords(
          address || account
        );
        if (isMounted.current) {
          dispatch({
            type: FETCH_STATE.SUCCESS,
            payload: decodeInviterRecord(data),
          });
        }
      } catch (err) {
        console.error(err);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetchData();
  }, [invitationContract, isMounted, account, address, updateByTimer]);

  return state;
};

export const useRegisteredInvitationCodeContains = () => {
  const invitationContract = useInvitationContract();
  const isValidInvitation = async (code) => {
    if (!code) {
      return false;
    }
    try {
      const result = await invitationContract.registeredInvitationCodeContains(
        ethers.utils.formatBytes32String(code)
      );
      return result;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  return { isValidInvitation };
};

export const useRegisterInviter = () => {
  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: null,
  });
  const invitationContract = useInvitationContract();
  const isMounted = useIsMounted();
  const { data: defaultInviterCode } = useDefaultInviterCode();

  const registerInviter = async ({ username, email, inviterCode }) => {
    console.log("registerInviter", username, email, inviterCode);
    try {
      const { utils } = ethers;
      if (!(invitationContract && defaultInviterCode && username && email)) {
        return;
      }
      dispatch({ type: FETCH_STATE.INIT });
      const tx = await invitationContract.registerInviter(
        utils.formatBytes32String(inviterCode || defaultInviterCode),
        username,
        email
      );
      message.info("Request sent");
      console.log("tx", tx);
      const receipt = await tx.wait();
      message.success("Transaction completed");
      console.log("receipt", receipt);
      if (isMounted.current) {
        dispatch({ type: FETCH_STATE.SUCCESS, payload: null });
      }
    } catch (error) {
      console.log(error);
      message.error("Transaction failed");
      if (isMounted.current) {
        dispatch({ type: FETCH_STATE.FAILURE });
      }
    }
  };

  return { state, registerInviter };
};
