import React from "react";
import { useTranslation, Trans } from "react-i18next";
import classNames from "classnames";
import MainContainer from "../../Components/UI/MainContainer/MainContainer";
import styles from "./Footer.module.scss";

const Footer = (props) => {
  const { t } = useTranslation();

  const showFooter = true;
  const mainClassName = !showFooter
    ? classNames(styles.footer, styles.hide)
    : styles.footer;

  return (
    <div id="contact" className={mainClassName}>
      <MainContainer className={styles.footInner}>
        <div className={styles.contactInfo}>
          <p>
            <Trans i18nKey={"footer-address"}>
              17870 Castleton street Ste. 215, City of Industry, CA 91748
            </Trans>
          </p>
          <p>
            {`${t("Email")}: `}
            <a href="mailto:support@bgfcapital.org">support@bgfcapital.org</a>
          </p>
          &copy; 2022 by BGF Capital
        </div>
      </MainContainer>
    </div>
  );
};

export default Footer;
