import React from "react";
import { useGlobalConfig } from "../../../hooks/useInvestPool";
import InvestmentItem from "./InvestmentItem/InvestmentItem";

import styles from "./ProfileMyInvestment.module.scss";

const ProfileMyInvestment = ({ investRecords, inviterRecords }) => {
  const liveRecords = investRecords?.liveRecords;
  const { data: globalConfig } = useGlobalConfig();
  return (
    <div className={styles.ProfileMyInvestment}>
      <h2 className={styles.title}>My Investment</h2>
      <div className={styles.investmentList}>
        {liveRecords.map((record) => (
          <InvestmentItem
            record={record}
            inviterRecords={inviterRecords}
            globalConfig={globalConfig}
            key={record.recordId}
          />
        ))}
      </div>
    </div>
  );
};

export default ProfileMyInvestment;
