import { Select } from "antd";
import classNames from "classnames";
import React, { useEffect, useState } from "react";
import ProfileMyInvestment from "../../Components/Profile/ProfileMyInvestment/ProfileMyInvestment";
import ProfileReferral from "../../Components/Profile/ProfileReferral/ProfileReferral";
import MainContainer from "../../Components/UI/MainContainer/MainContainer";
import { useIsRegistered, useInviterRecords } from "../../hooks/useInvitation";
import { useIsMobileView } from "../../hooks/useIsMobileView";
import { useInvestRecords } from "../../hooks/useInvestPool";
import { useHistory } from "react-router-dom";
import { useWallet } from "../../hooks/useWallet";
import styles from "./Profile.module.scss";

const PROFILE_TABS = {
  REFERRAL: 0,
  INVESTMENT: 1,
};

const PROFILE_TABS_INFO = {
  [PROFILE_TABS.REFERRAL]: {
    id: 0,
    text: "Referral",
    component: ProfileReferral,
  },
  [PROFILE_TABS.INVESTMENT]: {
    id: 1,
    text: "My Investment",
    component: ProfileMyInvestment,
  },
};

const Profile = () => {
  const isMobileView = useIsMobileView();
  const [currentTab, setCurrentTab] = useState(PROFILE_TABS.REFERRAL);
  const { data: investRecords } = useInvestRecords();
  const { data: isRegistered } = useIsRegistered();
  const { data: inviterRecords } = useInviterRecords();
  const { account } = useWallet();

  const history = useHistory();

  useEffect(() => {
    if (!account || isRegistered === false) {
      history.push("/");
    }
  }, [isRegistered, history, account]);

  const getDetail = () => {
    const Component = PROFILE_TABS_INFO[currentTab].component;
    return (
      <Component
        investRecords={investRecords}
        inviterRecords={inviterRecords}
      />
    );
  };

  return (
    <MainContainer className={styles.Profile}>
      <h2 className={styles.title}>Profile</h2>
      <hr />
      <div className={styles.contentWrap}>
        <div className={styles.tabList}>
          {isMobileView ? (
            <Select
              value={currentTab}
              onChange={(value) => {
                setCurrentTab(value);
              }}
            >
              {Object.values(PROFILE_TABS).map((tab) => (
                <Select.Option value={tab} key={tab}>
                  {PROFILE_TABS_INFO[tab].text}
                </Select.Option>
              ))}
            </Select>
          ) : (
            <>
              {Object.values(PROFILE_TABS).map((tab) => (
                <div
                  className={classNames(styles.tabItem, {
                    [styles.active]: currentTab === tab,
                  })}
                  key={tab}
                  onClick={() => {
                    setCurrentTab(tab);
                  }}
                >
                  {PROFILE_TABS_INFO[tab].text}
                </div>
              ))}
            </>
          )}
        </div>
        <div className={styles.profileDetailWrap}>{getDetail()}</div>
      </div>
    </MainContainer>
  );
};

export default Profile;
